import { POSTURE_TYPES } from 'configs/constants';
import React, { useEffect } from 'react';
import { useSelector } from 'react-redux';

interface Props {
  showTooltip: any;
  confirmTooltip: any;
}

const mappingText = {
  [POSTURE_TYPES.ALL]: 'Passen Sie die Videos Ihren Bewegungsmöglichkeiten an.',
  [POSTURE_TYPES.STANDING]:
    'Sie können nur Übungen im Stehen absolvieren? Passen Sie die Videos Ihren Bewegungsmöglichkeiten an.',
  [POSTURE_TYPES.SIT]:
    'Sie können nur Übungen im Sitzen absolvieren? Passen Sie die Videos Ihren Bewegungsmöglichkeiten an.',
  [POSTURE_TYPES.LIE]:
    'Sie können nur Übungen im Liegen absolvieren? Passen Sie die Videos Ihren Bewegungsmöglichkeiten an.',
};

const MobilityTooltip: React.FC<Props> = (props) => {
  const { showTooltip, confirmTooltip } = props;
  const postureType = useSelector((state: any) => state.settings.posture_type);

  useEffect(() => {
    if (showTooltip) {
      document.getElementsByTagName('html')[0].style.overflowY = 'hidden';
    } else {
      document.getElementsByTagName('html')[0].style.overflowY = 'overlay';
    }
  }, [showTooltip]);

  return (
    <div className="d-flex" style={{ position: 'relative' }}>
      {showTooltip && <div className="hb-backdrop" />}
      {props.children}
      {showTooltip && (
        <div>
          <div className="hb-mobility-tooltip">
            <div className="arrow" />
            <div className="title">Sind die Übungen zu schwer?</div>
            <div className="content">{mappingText[postureType]}</div>
            <div className="btn" onClick={confirmTooltip}>
              Verstanden
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default MobilityTooltip;
